  export const getcricketfixturedownload = (from = '', to = '') => {
    // const today = new Date()
    // const date = `${today.getFullYear()}_${(today.getMonth() + 1)}_${today.getDate()}`
    return async (dispatch) => {
      dispatch(getdownload())
      await http
        .get(from && to ? `/cricket/fixture/list?from=${from}&to=${to}&download=true` : '/cricket/reports/contestReport?download=true', { responseType: 'blob' })
        .then((response) => {
          notifySuccessadd('succesfully downloaded')
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `cricketFixture.csv` /*`contest.csv${date}`*/) 
          document.body.appendChild(link)
          link.click()
          dispatch({
            type:'SET_DOWNLOAD_OFF'
          })
        })
        .catch(() => {
          dispatch({
            type:'SET_DOWNLOAD_OFF'
          })
          notifyError("Download Failed")
        })
    }
  }

